<template>
  <v-container fluid grid-list-md>
    <v-layout row>
      <v-flex xs12 md3 v-for="(e, i) in services" :key="e.id">
        <v-card>
      
            <v-card-title>
            {{e.name}}
            </v-card-title>

            <v-list-item>
              <v-list-item-content>
                <v-list-item-title class="headline">Стоимость: <span class="green--text">{{e.price}}</span><v-icon color="success">mdi-currency-rub</v-icon></v-list-item-title>
              </v-list-item-content>
            </v-list-item>
            <v-card-text>
              {{e.description}}
            </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn icon color="error" @click="deleteService(i)">
              <v-icon>mdi-trash-can-outline</v-icon>
            </v-btn>
            <v-btn icon color="info" @click="editService(i)">
              <v-icon>mdi-pencil</v-icon>
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>

    </v-layout>
    <v-layout row justify-center class="my-4">
      <v-dialog v-model="dialog" max-width="800px" persistent>
        <template v-slot:activator="{on}">
          <v-btn color="primary" v-on="on">Добавить услугу</v-btn>
        </template>
        <v-card>
          <v-card-title>
            <span class="headline" v-if="!edit">Добавление услуги</span>
            <span class="headline" v-else>Редактирование услуги</span>
          </v-card-title>
          <v-card-text>
            <v-container grid-list-md>
              <v-form ref="forms" v-model="valid">
                <v-layout wrap>
                <v-flex xs12 sm6 md6>
                  <v-text-field 
                    label="Название услуги*" 
                    v-model="name"
                    :rules="[v => !!v || 'Поле не должно быть пустым']"
                    ></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md6>
                  <v-text-field
                    maxlength="6"
                    counter
                    type="number"
                    label="Стоимость услуги*" 
                    v-model.number="price"
                    prefix="rub"
                    :rules="[
                      v => !!v || 'Поле не должно быть пустым',
                      v => (v.toString() && v.toString().length <= 6)|| 'Силишком большое число'
                    ]"
                  ></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-textarea label="Описание" outlined v-model="description"></v-textarea>
                </v-flex>
              </v-layout>
              </v-form>
            </v-container>
            <small>* обязательные поля</small>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="closeModal">Закрыть</v-btn>
            <v-btn v-if="!edit" color="blue darken-1" text @click="saveServices" :loading="loading" :disabled="!valid">Сохранить</v-btn>
            <v-btn v-else color="blue darken-1" text @click="saveEditServices" :disabled="!valid">Сохранить изменения</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      valid: false,
      dialog: false,
      edit: false,
      name: '',
      price: 0,
      description: '',
      idx: null
    }
  },
  methods: {
    deleteService(i) {
      this.$store.dispatch('deleteServices', this.services[i].id)
    },
    editService(i) {
      this.dialog = true
      this.edit = true
      this.name = this.services[i].name
      this.price = this.services[i].price
      this.description = this.services[i].description
      this.idx = this.services[i].id
    },
    saveServices() {
      this.$store.dispatch('addServices', {name: this.name, price: this.price, description: this.description}) 
      this.zeroing()
    },
    saveEditServices() {
      this.$store.dispatch('editServices', {
        name: this.name,
        price: this.price,
        description: this.description,
        id: this.idx
      })
      this.zeroing()
    },
    closeModal() {
      this.zeroing()
      this.$refs.form.resetValidation()
    },
    zeroing() {
      this.dialog = false
      this.edit = false
      this.name = ''
      this.price = 0
      this.description = ''
      this.idx = null
    }
  },
  computed: {
    services() {
      return this.$store.getters.getServicesInfo
    },
    loading() {
      return this.$store.getters.loading
    }
  }
}
</script>
